body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  position: relative;
  width: 100%;
  max-width: 1800px;
  height: 100%;
  background-color: white;
}

.Linku {
  color: #1A62BA;
  font-size: 1.5vh;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
}

@media screen and (min-width: 1800px){
  .App {
    width: 100%;
    max-width: 1800px;
    height: 100%;
    background-color: white;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
}
.UserMenuBox {
    background-color: #c9c9c9;
    width: 100%;
    height: 100%;
    min-height: 100vh;
}

.UserMenuHeader {
    position: relative;
    width: 100%;
    height: 70px;
    background-color: #1A62BA;
}

.UserMenuPageTitle {
    position: absolute;
    top: 25%;
    left: 3%;
    color: white;
    font-weight: bold;
}

.UserMenuProfile {
    position: absolute;
    top: 25%;
    right: 3%;
}

.UserMenuProfileBox {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 35px;
    margin: 0px 23px 0px 23px;
    border-radius: 50%;
}

.UserMenuProfileImage {
    width: 50%;
    height: 50%;
    object-fit: contain;
}

.UserMenuProfileDropdown {
    background-color: white;
    width: 100%;
    height: auto;
    margin-top: 5px;
    padding: 2px 8px 2px 8px;
}

.UserMenuProfileDownloads {
    text-align: center;
    width: 100%;
    margin-bottom: 2px;
    border-bottom: 1px solid black;
}

.UserMenuProfileLogout {
    text-align: center;
    width: 100%;
}

.UserMenuInputBox {
    background-color: white;
    position: relative;
    width: 30%;
    height: 50%;
    top: 25%;
    margin: auto;
}

.UserMenuInput {
    width: 100%;
    height: 100%;
    padding-left: 8px;
    padding-right: 8px;
    font-size: 0.8em;
    color: black;
    border: none;
}

.UserMenuContentBox {
    width: 100%;
    height: 100%;
}

.UserMenuContent {
    position: relative;
    width: 60%;
    max-width: 700px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
}

.NoSearchResult {
    width: 100%;
    text-align: center;
    margin-top: 20px;
}

.UserMenuItem {
    margin-top: 20px;
    flex-basis: 33.333333%;
    height: auto;
    padding-left: 20px;
    padding-right: 20px;
}

.UserMenuItemContainer {
    background-color: white;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
}

.UserMenuItemImageBox {
    width: 100%;
    height: 100px;
}

.UserMenuItemImage {
    width: 100%;
    height: 100%;
}

.UserMenuItemContent {
    width: 100%;
    height: auto;
    margin-top: 10px;
    margin-bottom: 10px;
}

.UserMenuItemTitle {
    text-align: center;
    margin-bottom: 10px;
}

.UserMenuItemAuthor {
    text-align: center;
}

.UserSelectedBox {
    width: 50%;
    height: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    margin: auto;
}

.UserSelectedImageBox {
    width: 100%;
    height: 150px;
    margin-top: 20px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.UserSelectedImage {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.UserSelectedText {
    width: 100%;
    text-align: center;
    margin-top: 20px;
}

.UserSelectedButton {
    background-color: #1A62BA;
    color: white;
    width: 30%;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 20px auto auto auto;
    border: none;
}

.UserSelectedReviewBox {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 20px;
}

.UserSelectedReviewInput {
    width: 50%;
    height: 100%;
    padding: 10px;
    border: 1px solid black;
    margin-right: 20px;
}
.LoginBox {
    width: 100%;
    margin-top: 5vh;
    margin-bottom: 20vh;
}

.ErrorBox {
    display: block;
    width: 100%;
    height: 8vh;
    background-color: red;
    border-radius: 5px;
    margin-bottom: 3vh;
}

.ErrorBoxHide {
    display: none;
}

.ErrorText {
    width: 100%;
    height: 100%;
    color: white;
    text-align: center;
    line-height: 8vh;
    font-size: 1.5vh;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
}

.ErrorButton {
    position: absolute;
    top: 2%;
    right: 2%;
    width: 4%;
    height: 4%;
    object-fit: contain;
}

.LoginInputBox {
    position: relative;
